<template>
  <div class="source-channel">
    <!--  搜索  -->
    <el-form
      ref="sourceSearch"
      label-position="left"
      label-width="80px"
      :model="officialSearch"
      inline
    >
      <el-form-item label="菜单名称" class="search-item">
        <el-input
          v-model="officialSearch.name"
          clearable
          placeholder="请输入名称"
        />
      </el-form-item>
      <el-form-item style="float: right">
        <el-button type="primary" class="select-btn" @click="onSubmit"
          >查询</el-button
        >
        <el-button type="primary" class="select-btn" @click="resetForm"
          >重置</el-button
        >
      </el-form-item>
    </el-form>
    <!--  列表  -->
    <div class="list">
      <div class="title">
        <el-button type="primary" @click="$router.push({ name: 'addMenu' })"
          >新增父级菜单</el-button
        >
        <el-button type="primary" @click="$router.push({ name: 'addKeyword' })"
          >新增关键字</el-button
        >
        <el-button type="primary" @click="releaseShow = true">发布</el-button>
      </div>
      <el-table
        :data="list"
        style="width: 100%"
        border
        :empty-text="hasListInfo"
        :tree-props="{ children: 'children', hasChildren: 'hasChildren' }"
        row-key="id"
      >
        <el-table-column prop="name" label="菜单名称" align="center" />
        <el-table-column prop="url" label="跳转url（key）" align="center" />
        <el-table-column prop="created_at" label="创建时间" align="center" />
        <el-table-column fixed="right" label="操作" width="200" align="center">
          <template slot-scope="scope">
            <el-button
              type="text"
              size="small"
              @click="
                $router.push({ name: 'editMenu', params: { id: scope.row.id } })
              "
              >编辑</el-button
            >
            <el-button
              v-if="scope.row.parent_id === '0'"
              type="text"
              size="small"
              @click="
                $router.push({ name: 'addMenu', query: { id: scope.row.id } })
              "
              >添加子菜单</el-button
            >
            <el-button
              type="text"
              size="small"
              @click="
                delShow = true
                delText = '该菜单信息将不存在'
                rowInfo = scope.row
              "
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
    <div class="list">
      <div class="list-title">
        关键词列表
      </div>
      <el-table
        :data="keywordList"
        style="width: 100%"
        border
        :empty-text="hasListInfo"
      >
        <el-table-column prop="keyword" label="关键词" align="center" />
        <el-table-column
          prop="callback_type"
          label="返回数据格式"
          align="center"
        />
        <el-table-column prop="callback_data" label="返回数据" align="center" />
        <el-table-column prop="created_at" label="创建时间" align="center" />
        <el-table-column fixed="right" label="操作" width="200" align="center">
          <template slot-scope="scope">
            <el-button
              type="text"
              size="small"
              @click="
                $router.push({
                  name: 'editKeyword',
                  params: { id: scope.row.id }
                })
              "
              >编辑</el-button
            >
            <el-button
              type="text"
              size="small"
              @click="
                delShow = true
                delText = '该关键词信息将不存在'
                rowInfo = scope.row
              "
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
    </div>
    <el-dialog
      title="删除提示"
      :visible.sync="delShow"
      width="400px"
      center
      top="30vh"
    >
      <p class="center">删除后，{{ delText }}， <br />请确定是否删除？</p>
      <span slot="footer" class="dialog-footer">
        <el-button @click="delConfirm">确 定</el-button>
        <el-button type="primary" @click="delShow = false">取 消</el-button>
      </span>
    </el-dialog>
    <el-dialog
      title="发布提示"
      :visible.sync="releaseShow"
      width="400px"
      center
      top="30vh"
    >
      <p class="center">
        请确认是否发布？
      </p>
      <span slot="footer" class="dialog-footer">
        <el-button @click="release">确 定</el-button>
        <el-button type="primary" @click="releaseShow = false">取 消</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
import {
  getOfficialList,
  delMenu,
  releaseMenu,
  getKeyword
} from '@/api/officialAccount'
import { delKeyword } from '../../../api/officialAccount'
export default {
  data() {
    return {
      isOpen: false,
      list: [],
      delShow: false,
      btnText: '',
      rowInfo: '',
      delText: '',
      hasListInfo: '暂无数据',
      releaseShow: false,
      keywordList: [],
      officialSearch: {
        name: ''
      }
    }
  },
  created() {
    this.getOfficialList()
    this.getKeyword()
  },
  methods: {
    onSubmit() {
      this.currentPage = 1
      this.getOfficialList(this.officialSearch)
    },
    resetForm() {
      this.officialSearch = {
        name: ''
      }
      this.getOfficialList()
    },
    async delConfirm() {
      if (this.delText === '该菜单信息将不存在') {
        const { code, message } = await delMenu(this.rowInfo.id)
        if (code === 1000) {
          this.$message.success(message)
          this.delShow = false
          this.getOfficialList(this.officialSearch)
        }
      } else {
        const { code, message } = await delKeyword(this.rowInfo.id)
        if (code === 1000) {
          this.$message.success(message)
          this.delShow = false
          this.getKeyword(this.officialSearch)
        }
      }
    },
    async release() {
      const { code, data } = await releaseMenu()
      if (code === 1000) {
        if (data.errcode === 0) {
          this.releaseShow = false
          this.$message.success('发布成功')
        } else {
          this.$message.error(data.errcode + '')
        }
      }
    },
    async getOfficialList(search) {
      let params = {
        pageSize: this.pageSize,
        page: this.currentPage
      }
      if (search) {
        if (search.name) params.name = search.name
        this.hasListInfo = '暂无数据，请重新输入关键词'
      } else {
        this.hasListInfo = '暂无数据'
      }
      const { data, code } = await getOfficialList(params)
      if (code === 1000) {
        this.count = data.count
        this.list = data
          .filter(v => {
            return v.parent_id === '0'
          })
          .map((item, index) => {
            return {
              id: item._id,
              name: item.name,
              type: item.type,
              url: item.key || item.url || '/',
              callback_type: item.callback_type || '/',
              callback_data: item.callback_data || '/',
              created_at: this.formatTime(item.created_at),
              parent_id: item.parent_id,
              children: data
                .filter(h => {
                  return h.parent_id === item._id
                })
                .map(info => {
                  return {
                    id: info._id,
                    name: info.name,
                    type: info.type,
                    url: info.key || info.url || '/',
                    callback_type: info.callback_type || '/',
                    callback_data: info.callback_data || '/',
                    created_at: this.formatTime(info.created_at),
                    parent_id: info.parent_id
                  }
                })
            }
          })
      }
    },
    async getKeyword() {
      const { code, data } = await getKeyword()
      if (code === 1000) {
        this.keywordList = data.list.map(item => {
          return {
            callback_type: item.callback_type,
            callback_data: item.callback_data,
            keyword: item.keyword,
            created_at: this.formatTime(item.created_at),
            id: item._id
          }
        })
      }
    }
  }
}
</script>

<style lang="scss" scoped>
.source-channel {
  display: flex;
  flex-wrap: wrap;
  font-size: 14px;
  margin-bottom: 20px;
  /deep/form {
    width: 100%;
    .el-date-editor {
      padding: 0 10px;
      width: 220px;
      height: 32px;
      .el-range-input {
        width: 85px;
        height: 30px;
      }
    }
  }
  .search-item {
    color: #17233d;
    &:not(:nth-child(4)) {
      margin-right: 50px;
    }
    /deep/input {
      width: 220px;
      height: 32px;
      line-height: 32px;
    }
    /deep/span {
      top: -3px;
    }
    /deep/.el-input__suffix {
      top: 0;
    }
  }
  .select-btn {
    padding: 0 20px;
    width: 68px;
    height: 32px;
  }
  .arrow {
    float: right;
  }
  .list {
    margin: 20px 0 0;
    width: 100%;
    .title {
      line-height: 32px;
      text-align: right;
      /deep/button {
        margin-bottom: 10px;
        padding: 6px 20px;
        height: 32px;
      }
    }
    .list-title {
      line-height: 50px;
      font-size: 18px;
      font-weight: 600;
    }
    /deep/.el-dialog__wrapper {
      button {
        padding: 6px 20px;
        height: 32px;
      }
      .el-dialog__headerbtn {
        top: 10px;
        right: 5px;
      }
    }
    .pagination {
      margin-top: 50px;
      text-align: center;
    }
  }
  /deep/.cell {
    overflow: hidden;
    -ms-text-overflow: ellipsis;
    text-overflow: ellipsis;
    *:not(button) {
      display: inline-block;
      width: 100%;
    }
    .el-table__expand-icon,
    .el-table__indent,
    .el-table__placeholder {
      width: auto;
    }
  }
  .center {
    line-height: 20px;
    text-align: center;
  }
  .button-wrap {
    button {
      height: 32px;
      line-height: 0;
    }
  }
  .danger-tip {
    margin-top: 10px;
    line-height: 16px;
    color: #f00;
  }
  .el-dialog__wrapper {
    /deep/.el-input__inner {
      width: 220px;
    }
  }
  .demo-ruleForm {
    /deep/.el-input {
      width: 220px;
    }
  }
}
</style>
