import request from '@/utils/officialRequest'

export function getOfficialList(params) {
  return request({
    url: '/wx_wechat/wx_menu/list',
    method: 'get',
    params
  })
}

export function createMenu(data) {
  return request({
    url: '/wx_wechat/wx_menu',
    method: 'post',
    data
  })
}

export function getMenuDetail(id, params) {
  return request({
    url: '/wx_wechat/wx_menu/' + id,
    method: 'get',
    params
  })
}

export function putMenuDetail(id, data) {
  return request({
    url: '/wx_wechat/wx_menu/' + id,
    method: 'put',
    data
  })
}

export function delMenu(id) {
  return request({
    url: '/wx_wechat/wx_menu/' + id,
    method: 'delete'
  })
}

export function releaseMenu(data) {
  return request({
    url: '/wx_wechat/release/wx_menu',
    method: 'post',
    data
  })
}

export function getMedia(data) {
  return request({
    url: '/wx_wechat/wx_material',
    method: 'post',
    data
  })
}

export function createKeyword(data) {
  return request({
    url: '/wx_wechat/wx_keyword_callback',
    method: 'post',
    data
  })
}

export function getKeyword() {
  return request({
    url: '/wx_wechat/wx_keyword_callback',
    method: 'get'
  })
}

export function getKeywordDetail(id) {
  return request({
    url: '/wx_wechat/wx_keyword_callback/' + id,
    method: 'get'
  })
}

export function editKeyword(id, data) {
  return request({
    url: '/wx_wechat/wx_keyword_callback/' + id,
    method: 'put',
    data
  })
}

export function delKeyword(id) {
  return request({
    url: '/wx_wechat/wx_keyword_callback/' + id,
    method: 'delete'
  })
}
